[data-animation*="ripple"] {
  position: relative;
  overflow: hidden;
}

[data-animation*="ripple"] .ripple-circle {
  position: absolute;
  z-index: 20;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  width: 112px;
  height: 112px;
  background: rgba(0, 0, 0, 0.1);
  animation: ripple 0.5s linear forwards;
  pointer-events: none;
}

@keyframes ripple {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(2);
  }
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 0 10px;
  margin: 0;
  border: none;
  min-width: 40px;
  height: 40px;
  color: #333;
  background: #eee;
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 40px;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  transition: background 0.175s ease, box-shadow 0.175s ease, opacity 0.175s ease;
  cursor: pointer;
  user-select: none;
}

.btn:active {
  color: #333;
  background: #dadada;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.btn_block {
  width: 100%;
}

.btn_lg {
  padding: 0 15px;
  min-width: 60px;
  height: 60px;
  font-size: 19px;
  line-height: 60px;
}

.btn_green {
  color: #fff;
  background: #1a8c35;
}

.btn_green:active {
  color: #fff;
  background: #1a8c35;
}

.btn_red {
  color: #fff;
  background: #e32117;
}

.btn_red:active {
  color: #fff;
  background: #e32117;
}

.btn_primary {
  color: #fff;
  background: #9784ff;
}

.btn_primary:active {
  color: #fff;
  background: #816bff;
}

.btn_primary[data-animation*="ripple"] .ripple-circle {
  background: rgba(255, 255, 255, 0.1);
}

.btn:disabled, .btn.is-disabled {
  opacity: 0.5;
  pointer-events: none;
}

.panel-button {
  padding: 20px;
  background: #fff;
  font-size: 0;
}

.panel-button__inner {
  margin: 0 auto;
  max-width: 1200px;
}

.panel-button__inner:after {
  content: " ";
  display: table;
  clear: both;
}

.panel-button__left {
  float: left;
}

.panel-button__right {
  float: right;
}

.panel-button__btn {
  position: relative;
  display: inline-block;
  vertical-align: top;
  min-width: 120px;
}

.panel-button__btn:not(:first-child) {
  margin-left: 20px;
}
